import clsx from 'clsx';

import {
  TableBodyProps,
  TableCellProps,
  TableHeadCellProps,
  TableHeadProps,
  TableProps,
  TableRowProps,
} from './table.interface';

const Table = ({ children, className, maxWidth, maxHeight, fullWidth, ...props }: TableProps) => {
  return (
    <div
      className={clsx('zep-overflow-scroll zep-w-full sm:zep-w-auto', className, {
        'sm:zep-w-full': fullWidth,
      })}
      style={{
        maxWidth,
        maxHeight,
      }}
      data-testid="zep-table"
    >
      <table
        className={clsx('zep-table zep-w-full sm:zep-w-auto', {
          'sm:zep-w-full': fullWidth,
        })}
        {...props}
      >
        {children}
      </table>
    </div>
  );
};

const TableBody = ({ children, ...props }: TableBodyProps) => {
  return <tbody {...props}>{children}</tbody>;
};

const TableCell = ({ children, className, align = 'left', dataLabel, ...props }: TableCellProps) => {
  return (
    <td
      className={clsx(
        'zep-flex zep-justify-between sm:zep-table-cell zep-relative md:zep-px-1.5 zep-p-0.75 sm:zep-p-0.5 zep-bg-greyscale-0 sm:zep-whitespace-nowrap zep-group',
        className,
      )}
      style={{
        textAlign: align,
      }}
      {...props}
    >
      {dataLabel && (
        <>
          <span className="zep-text-left zep-inline-block sm:zep-hidden sm:zep-py-0.5 zep-w-full zep-typography-bodyStrong zep-text-typography-dark-100">
            {dataLabel}
          </span>
          <hr className="zep-hidden group-first:zep-block sm:group-first:zep-hidden zep-absolute zep-border-t-2 zep-bottom-[0] zep-left-[0] zep-right-[0] zep-text-greyscale-900" />
        </>
      )}
      <span className="zep-text-right sm:zep-text-unset zep-inline-block sm:zep-py-0.5 zep-w-full zep-typography-bodyText zep-text-typography-dark-100">
        {children}
      </span>
      <hr className="group-first:zep-hidden sm:group-first:zep-block zep-absolute zep-h-[1px] zep-bottom-[0] zep-left-[0] zep-right-[0] zep-text-greyscale-400" />
    </td>
  );
};

const TableHead = ({ children, className, ...props }: TableHeadProps) => {
  return (
    <thead className={clsx('zep-hidden sm:zep-table-row-group', className)} {...props}>
      {children}
    </thead>
  );
};

const TableHeadCell = ({ children, className, align = 'left', ...props }: TableHeadCellProps) => {
  return (
    <th
      className={clsx(
        'zep-relative md:zep-px-1.5 zep-p-0.75 sm:zep-p-0.5 zep-bg-greyscale-0 sm:zep-whitespace-nowrap',
        className,
      )}
      style={{
        textAlign: align,
      }}
      {...props}
    >
      <span className="zep-inline-block sm:zep-py-0.5 zep-w-full zep-typography-bodyStrong zep-text-typography-dark-100">
        {children}
      </span>
      <hr className="zep-absolute zep-border-t-2 zep-bottom-[0] zep-left-[0] zep-right-[0] zep-text-greyscale-900" />
    </th>
  );
};

const TableRow = ({ children, className, ...props }: TableRowProps) => {
  return (
    <tr className={clsx('zep-flex zep-flex-col zep-mb-2 sm:zep-table-row sm:zep-sm-[0px]', className)} {...props}>
      {children}
    </tr>
  );
};

Table.Body = TableBody;
Table.Cell = TableCell;
Table.Head = TableHead;
Table.HeadCell = TableHeadCell;
Table.Row = TableRow;

export { Table };
